.button{@apply flex h-[24px] w-[24px] items-center justify-center rounded-[5px] bg-[#EFEFEF] transition-colors ease-linear max-sm:h-[19px] max-sm:w-[19px]}.button .icon{@apply fill-[#B1B1B1] stroke-[#B1B1B1] transition-colors ease-linear}.button .icon_add{@apply h-[14px] w-[14px] max-sm:h-[9px] max-sm:w-[9px]}.button .icon_delete{@apply h-[14px] w-[14px] max-sm:h-[9px] max-sm:w-[8px]}.button .icon_edit{@apply h-[14px] w-[14px] max-sm:h-[9px] max-sm:w-[9px]}.button:hover .icon{@apply fill-[#4674B8] stroke-[#4674B8]}.button:active{@apply bg-[#EEEEF8]}.button:where(:global(.dark),:global(.dark) *){@apply bg-[#474747]}.button:where(:global(.dark),:global(.dark) *) .icon{@apply fill-[#B1B1B1] stroke-[#B1B1B1]}.button:where(:global(.dark),:global(.dark) *):hover .icon{@apply fill-[#CBCBCB] stroke-[#CBCBCB]}.button:where(:global(.dark),:global(.dark) *):active{@apply bg-[#8A8A8A]}