@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");

html,
body {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
}

body {
  position: relative;
}

#mainMenu {
  height: calc(100vh - 24px);
}

#backdrop {
  visibility: hidden;
  background-color: #141f33;
  opacity: 0;
}

#mainMenu:hover + #backdrop {
  visibility: visible;
  opacity: 0.7;
}

.scroll::-webkit-scrollbar {
  width: 6px;
  /* ширина скрола */
  height: 6px;
}

/* Кнопки стрілок */
.scroll::-webkit-scrollbar-button {
  background: #ffffff;
  width: 0;
  height: 0;
}

/* Шпильки треку */
.scroll::-webkit-scrollbar-thumb {
  background: #1b77d29e;
  border-radius: 3px;
}

/* Фон треку */
.scroll::-webkit-scrollbar-track {
  background: #ffffff;
}

.overflow-gradient {
  background: linear-gradient(270deg, #ffffff 0%, rgba(139, 139, 141, 0) 30%);
}

.dark .overflow-gradient {
  background: linear-gradient(270deg, #2c2d31 0%, rgba(139, 139, 141, 0) 30%);
}

.custom-responsive-domain {
  max-width: 400px;
}

@media not all and (min-width: 825px) {
  .custom-responsive-domain {
    max-width: clamp(11.25rem, -29.875rem + 85.45vw, 14.188rem);
  }
}

@media not all and (min-width: 768px) {
  .custom-responsive-domain {
    max-width: 227px;
  }
}

@media not all and (min-width: 415px) {
  .custom-responsive-domain {
    max-width: clamp(8.125rem, -21.758rem + 127.5vw, 11.313rem);
  }
}

@media not all and (min-width: 415px) {
  .custom-responsive-email {
    max-width: clamp(8.75rem, -17.617rem + 112.5vw, 11.563rem);
  }
}
